<template>
  <div id="checkout">
    <div class="checkout">
      <div class="checkout__main">
        <SfSteps :active="currentStep" @change="updateStep($event)">
          <SfStep name="جزییات">
            <SfPersonalDetails
              :value="personalDetails"
              @input="personalDetails = $event"
              :characteristics="characteristics"
              headingTitle="اطلاعات شخصی"
              :inputsLabels="['نام', 'نام خانوادگی', 'ایمیل']"
              :additionalDetails="''"
            >
              <template #log-in>{{ "" }}</template>
              <template #create-account>{{ "" }}</template>
            </SfPersonalDetails>
          </SfStep>
          <SfStep name="حمل و نقل">
            <SfShipping
              :value="shipping"
              :shipping-methods="shippingMethods"
              :countries="countries"
              @input="shipping = $event"
              headingTitle="حمل و نقل"
              methodsHeadingTitle="روش حمل و نقل"
              :inputsLabels="inputsLabels"
              selectLabel="کشور"
            />
          </SfStep>
          <SfStep name="پرداخت">
            <SfPayment
              :paymentMethods="paymentMethods"
              :shipping="shipping"
              :countries="countries"
              :months="months"
              :years="years"
              @input="payment = $event"
              billingHeading="آدرس سفارش دهنده"
              :billingInputsLabels="paymentInputsLabels"
              sameAsShippingCheckboxLabel="آدرس یکسان با حمل و نقل"
              paymentHeading="روش پرداخت"
              billingSelectLabel="کشور"
            />
          </SfStep>
          <SfStep name="تایید سفارش">
            <SfConfirmOrder
              :order="getOrder"
              order-title="Order details"
              orderTitle="جزییات سفارش"
              :properties-names="[
                'هزینه محصولات',
                'هزینه حمل ونقل',
                'هزینه کل',
              ]"
              :table-headers="[
                'Size',
                'Description',
                'Quantity',
                'Colour',
                'Amount',
              ]"
              @click:edit="currentStep = $event"
            >
              <template #table>{{ "" }}</template>
              <template #checkbox>{{ "" }}</template>
            </SfConfirmOrder>
          </SfStep>
        </SfSteps>
      </div>
      <div class="checkout__aside">
        <transition name="sf-fade">
          <SfOrderSummary
            v-if="currentStep <= 2"
            key="order-summary"
            class="checkout__aside-order"
            :order="getOrder"
            order-title="سفارش"
            :order-title-level="3"
            :properties-names="[
              'تعداد',
              'هزینه ',
              'هزینه حمل و نقل',
              'هزینه کل',
            ]"
            :characteristics="characteristics"
          >
            <template #promo>{{ "" }}</template>
          </SfOrderSummary>
          <CustomSfOrderReview
            v-else
            key="order-review"
            class="checkout__aside-order"
            :order="getOrder"
            review-title="سفارش"
            :review-title-level="3"
            button-text=""
            :characteristics="characteristics"
            @click:edit="currentStep = $event"
          >
          </CustomSfOrderReview>
        </transition>
      </div>
    </div>
    <div class="actions">
      <SfButton
        class="sf-button--full-width actions__button"
        data-testid="next-button"
        @click="nextStep"
        >{{ steps[currentStep] }}</SfButton
      >
      <SfButton
        class="
          sf-button--full-width sf-button--underlined
          actions__button
          smartphone-only
        "
        @click="checkoutBack"
        >برگشت</SfButton
      >
    </div>
  </div>
</template>
<script>
import {
  SfSteps,
  SfButton,
  SfPersonalDetails,
  SfShipping,
  SfPayment,
  SfConfirmOrder,
  SfOrderSummary,
  SfOrderReview,
  SfShippingDetails,
} from "@storefront-ui/vue";
import CustomSfOrderReview from "@/components/CustomSfOrderReview.vue";
export default {
  name: "CheckoutView",
  components: {
    SfSteps,
    SfPersonalDetails,
    SfShipping,
    SfPayment,
    SfConfirmOrder,
    SfOrderSummary,
    SfOrderReview,
    SfButton,
    SfShippingDetails,
    CustomSfOrderReview,
  },
  data() {
    return {
      countries: ["ایران"],
      months: [],
      years: [],
      currentStep: 0,
      steps: [
        "حمل و نقل",
        "انتخاب روش پرداخت",
        "پرداخت سفارش",
        "تایید و پرداخت",
      ],
      personalDetails: {
        firstName: "مهران",
        lastName: "عارفخانی",
        email: "test@gmail.com",
      },
      shipping: {
        firstName: "مهران",
        lastName: "عارفخانی",
        streetName: "خیابان عطار",
        apartment: "بلوک ۴",
        city: "نیشابور",
        state: "خراسان رضوی",
        zipCode: "12334",
        country: "ایران",
        phoneNumber: "+989381234567",
        shippingMethod: {
          price: "$0.00",
        },
      },
      payment: {
        sameAsShipping: true,
        firstName: "احسان",
        lastName: "داوودی",
        streetName: "خیام",
        apartment: "بلوک۳",
        city: "تهران",
        state: "تهران",
        zipCode: "12234",
        country: "",
        phoneNumber: "+989381234567",
        paymentMethod: "",
        invoice: false,
        cardNumber: "",
        cardHolder: "",
        cardMonth: "",
        cardYear: "",
        cardCVC: "",
        cardKeep: false,
      },
      order: {
        password: "",
        createAccount: false,
        firstName: "محمد",
        lastName: "حسینی",
        email: "john.dog@gmail.com",
        orderItems: this.$store.state.cartItems,
      },
      paymentMethods: [
        // {
        //   label: "Visa Debit",
        //   value: "debit",
        // },
        {
          label: "پرداخت آنلاین",
          value: "online",
        },
        // {
        //   label: "Visa Electron",
        //   value: "electron",
        // },
        {
          label: "پرداخت نقدی",
          value: "cash",
        },
        // {
        //   label: "Check",
        //   value: "check",
        // },
      ],

      inputsLabels: [
        "نام",
        "نام خانوداگی",
        "خیابان",
        "شهر",
        "استان",
        "کد پستی",
        "شماره تماس",
      ],
      paymentInputsLabels: [
        "نام",
        "نام خانوادگی",
        "خیابان",
        "پلاک",
        "شهر",
        "استان",
        "کد پستی",
        "شماره تماس",
      ],
      shippingMethods: [
        {
          isOpen: false,
          price: "رایگان",
          delivery: "ارسال در یک هفته کاری",
          label: "ارسال طولانی",
          value: "store",
          description:
            "Novelty! From now on you have the option of picking up an order in the selected InPack parceled. Just remember that in the case of orders paid on delivery, only the card payment will be accepted.",
        },
        {
          isOpen: false,
          price: "$9.90",
          delivery: "ارسال در یک روز کاری",
          label: "ارسال سریع",
          value: "home",
          description:
            "Novelty! From now on you have the option of picking up an order in the selected InPack parceled. Just remember that in the case of orders paid on delivery, only the card payment will be accepted.",
        },
      ],
      buttonNames: [
        { name: "Go to shipping" },
        { name: "Go to payment" },
        { name: "Review Order" },
        { name: "Place my order" },
      ],
      characteristics: [],
    };
  },
  computed: {
    getOrder() {
      return {
        ...this.order,
        ...this.personalDetails,
        shipping: { ...this.shipping },
        payment: { ...this.payment },
      };
    },
  },
  watch: {
    shipping(newVal, oldVal) {
      if (newVal.shippingMethod != oldVal.shippingMethod) {
        const method = this.shippingMethods.find(
          (method) => method.value === newVal.shippingMethod
        );
        return method
          ? (newVal.shippingMethod = method)
          : (newVal.shippingMethod = { price: "$0.00" });
      }
    },
  },
  methods: {
      checkoutBack(){
             if(this.currentStep==0){
                this.$router.push('/cart')
             }
             else{
              this.currentStep--;

             }


      },
    nextStep() {
      if (this.currentStep < this.steps.length - 1) {
        this.currentStep++;
      } else {
        this.$router.push("/");
      }
    },
    updateStep(next) {
      // prevent to move next by SfStep header

      if (next < this.currentStep) {
        this.currentStep = next;
      }
    },
  },
};
</script>
